@mixin link_focus ($offset: 4px, $background: true) {
  transition: background, text-decoration 0.3s;
 
  &:focus {
    color: $color-font;
    outline: 2px dotted inherit;
    outline-offset: 1px;
    text-decoration: underline;
    background-color: $color-brand--faded;

    @if not ($background == true) {
      background: none;
    } 
  }

  &:focus:not(:focus-visible) {
    text-decoration: none;
    outline-color: transparent;
  }

  &:focus-visible {
    color: $color-font;
    outline-color: transparent;
    outline-offset: 1px;
    text-decoration-thickness: 3px;
    text-decoration-color: $color-brand--bright;
    text-underline-offset: $offset;
    background-color: $color-brand--faded;

    @if not ($background == true) {
      background: none;
    } 
  }
}