.uol-form-search {
  color: $color-font;
  font-size: $base-font-size * 1.125;
  margin-bottom: 1.75rem;

  @include media(">=uol-media-m") {
    display: flex;
    flex-wrap: nowrap;
    border-radius: 0.75rem;
    margin-bottom: $spacing-6;
  }

  @include media(">=uol-media-l") {
    border-radius: 12px;
    font-size: $base-font-size * 1.25;
  }

  .uol-form-input-container {
    flex: 1;
  }

  .uol-form-input-wrapper {
    display: block;
    margin-top: $spacing-2;
    position: relative;

    svg {
      fill: windowText;
      position: absolute;
      top: 0.9rem;
      left: 0.9rem;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: calc(#{$spacing-4} + 1px);
      left: 1px;
      border-bottom: 4px solid $color-blue--bright;
      width: 0;
      border-radius: 0 0 4px 4px;
      transition: width 0.25s ease-in;

      @include media(">=uol-media-m") {
        bottom: 1px;
      }
    }

    &:focus-within {

      &::before {
        width: calc(100% - 2px);
      }
    }
  }

  .uol-button {
    @include button_focus();

    box-sizing: border-box;
    padding: $spacing-4;
    line-height: 0.9;
    height: 3.125rem;
    border: 1px solid $color-brand-2--dark;

    @include media("<uol-media-m") {
      width: 100%;
    }
  }

  .uol-form-input--search {
    @include font-size-responsive(1rem, 1.125rem, 1.25rem);
    @include line-height-responsive(1.25, 1.25, 1.1);

    &:focus {
      outline: 2px dotted transparent;
    }

    .no-csspositionsticky & {
      &:focus {
        outline: 1px solid $color-blue--bright;
      }
    }

    height: 3.125rem;
    width: 100%;
    padding-left: 2.875rem;
    border: 1px solid $color-border;

    &::placeholder {
      color: $color-font;
    }

    @include media("<uol-media-m") {
      margin-bottom: $spacing-4;
    }

    @include media(">=uol-media-m") {
      margin-right: $spacing-4;
    }

    @include media(">=uol-media-l") {
      margin-right: $spacing-5;
    }

    @include media(">=uol-media-xl") {
      margin-right: $spacing-6;
    }

    &[aria-invalid="true"] {
      border: 2px solid $color-alert--error;
      box-shadow:
        0 3px 6px rgba(0, 0, 0, 0.15),
        0 2px 4px rgba(0, 0, 0, 0.12);
    }
  }
}

  .uol-form-input__hint {
    padding-top: 0.5rem;
    padding-bottom: 0.875rem;
  }

.search-button-wrapper {
  align-self: flex-end;

  @include media(">=uol-media-m") {
    margin-left: $spacing-4;
  }

  @include media(">=uol-media-l") {
    margin-left: $spacing-5;
  }
}
