.uol-form-input {

  &[type="search"] {
    -webkit-appearance: none;
  }
}

  .uol-form-input__label {
    display: inline-block;
    width: 100%;
    font-weight: $font-weight-bold--sans-serif;
  }

  .uol-form-input__hint {
    display: inline-block;
    width: 100%;
    font-weight: $font-weight-regular--sans-serif;
    padding: $spacing-4 0;
  }

  .uol-form-input__error {
    display: block;
    margin-bottom: $spacing-2;
    padding: $spacing-2 $spacing-4 $spacing-2 44px;
    color: $color-white;
    background: url('../img/alert-circle-outline.svg') no-repeat 13px 50%;
    background-color: $color-alert--error;
    border-radius: 6px;
    font-weight: $font-weight-medium--sans-serif;

    @include media(">uol-media-m") {
      font-weight: $font-weight-regular--sans-serif;
    }
  }
